a {
    text-decoration: none;
}

.login_cart_cc ul li a i {
    font-size: 15px;
}
.dropbtn {
    color: white;
    font-size: 16px;
    border: none;
  }
  
  /* The container <div> - needed to position the dropdown content */
  .dropdown {
    position: relative;
    display: inline-block;
  }
  
  /* Dropdown Content (Hidden by Default) */
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 2;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  .dropdown-content a:hover ,.dropdown-content a:active {background-color: #ddd;}
  .dropdown:hover .dropdown-content {display: block;}
  
  
.Navbar{
    position: sticky;
    top: 0;
    z-index: 2;
}

.dropdown-content{
    z-index: 4;
}
